.category-about {
  &__wrapper {
    background-color: #F2F3F5;
    border-radius: 3%;
    padding: 30px 25px; 
    @include screen(laptop) {
      border-radius: 46px;
      padding: 65px 70px;
    }
    h2 {
      font-family: $gotham;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      @include screen(laptop) {
        font-size: 24px;
        line-height: 31px;
      }
    }
    p {
      margin-top: 10px;
      font-size: 14px;
      line-height: 23px;
      @include screen(laptop) {
        font-size: 18px;
        margin-top: 20px;
        line-height: 28px;
      }
    }
  }
}