.brands {
  background-color: #F2F3F5;
  overflow: hidden;
  @include screen(laptop) {
    background-color: transparent;
  }
  &__slider {
    padding: 22px 0;
    @include screen(laptop) {
      padding: 25px 0;
      border-top: 1px solid #DAD8E1;
      border-bottom: 1px solid #DAD8E1;
    }
    .swiper {
      overflow: visible;
      @include screen(laptop) {
        overflow: hidden;
        padding: 0 40px;
        margin: 0 -40px;
      }
      &-wrapper {
        align-items: stretch;
        transition-timing-function: linear;
      }
      &-slide {
        height: auto;
        padding: 15px 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;
        background-color: $white;
        @include screen(laptop) {
          border-radius: 0;
          padding: 0;
          background-color: transparent;
        }
        img {
          max-height: 40px;
          max-width: 100px;
          object-fit: contain;
          width: 100%;
        }
      }
    }
    .swiper-button-prev,
    .swiper-button-next {
      margin-top: 0;
      width: auto;
      height: auto;
      top: 50%;
      transform: translateY(-50%);
      display: none;
      @include screen(laptop) {
        display: block;
      }
      &:after,
      &:before {
        display: none;
      }
    }
  }
}